@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap);
.profile {
    text-align: center;
}

.profile img {
    border: 10px solid #2e2c2d;
    border-radius: 50%;
    max-width: 500px;
}

.profile h2,
.profile p {
    text-align: center;
    color: #2e2c2d;
}

@media (prefers-color-scheme: dark) {
    .profile h2,
    .profile p {
        color: #fbfbfb;
    }

    .profile img {
        border-color: #fbfbfb;
    }
}
.repos>ul {
    display: grid;
    grid-gap: 16px;
    gap: 16px;
}

.repo-link {
    color: #2e2c2d;
}

.repo {
    background-color: #fbfbfb;
    padding: 16px;
    border-radius: 8px;
}

.repo:hover {
    outline: 4px solid #2e2c2d;
}

.repo-header {
    display: flex;
    align-items: center;
    border-bottom: 2px solid #2e2c2d;
    padding-bottom: 8px;
}

.repo-header img {
    width: 32px;
    margin-right: 16px;
}

.repo-body {
    padding-top: 8px;
}

@media (prefers-color-scheme: dark) {
    .repo {
        background-color: #2e2c2d;
        color: #fbfbfb;
    }

    .repo:hover {
        outline: 4px solid #fbfbfb;
    }

    .repo-header {
        border-bottom: 2px solid #fbfbfb;
    }
    
}
.links ul {
    display: flex;
    justify-content: center;
}

.links li {
    margin: 0px 5px;
    max-width: 64px;
}

@media (prefers-color-scheme: dark) {
    .links li #github {
        -webkit-filter: invert(1) drop-shadow(0 0 0 black);
                filter: invert(1) drop-shadow(0 0 0 black);
    }
}
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style: none;
    font-family: 'JetBrains Mono', monospace;
}

a {
    text-decoration: none;
}

body {
    background-color: #e7e7e7;
    margin: 8px 0px;
}

#root {
    margin: 0 auto;
    justify-content: center;
    max-width: 1200px;
}

img {
    width: 100%;
}

h1 {
    font-size: 48px;
}

h2 {
    font-size: 36px;
}

h3 {
    font-size: 24px;
}

p,
a,
li {
    font-size: 18px;
}

section h1 {
    text-align: center;
}

section {
    padding: 8px;
}

@media (prefers-color-scheme: dark) {
    body {
        background: #151515;
    }
}
